
.footer-container
{
	display: flex;
	justify-content: space-between;
	align-items: center;
}

@media only screen
   and (max-width : 768px)
{
	.footer-container
	{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
}
.aboutus_subdiv
{
  width: 70vw;
  height: 85vh;
}
.subhead{
  font-family: 'Open Sans Condensed', sans-serif;
  letter-spacing: 0.5px;
  text-align: left;
  font-size:20px;
}
.testimonial{
  display: grid;
  grid-template-columns:40% 60%;
}
.processbox{
  display: flex;
	background-image: url("../../Images/appbackground.png");
	 background-repeat: no-repeat;
	 width: 100vw;
	 height:auto;
}
.who_dis{
margin-right: 10%;
}

.main-div{
   background-image: url("../../Images/aboutusbackground.jpg");
   background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 100vw;
    height:auto;
    opacity: 0.8;
    padding:5%;
}
.feature_list
{
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 20px;
  list-style:none;
  text-align:left;
  display: grid;
  grid-template-columns: 50% 50%;
}
.aboutus_maindiv{
  padding-left: 10%;
}
.execute{
  display: grid;
  grid-template-columns: 50% 50%;
}
.process{
  width:30vw;
  height:auto;
	background-color: #121312;
}
.bestservices{
  width:35vw;
}

.category-button
{
  position: relative;
  width: 150px;

  .butt1
  {
    width: 120px;
    height: 50px;
    border: none;
    color:white;
    background:gray;
    // border: 1px solid black;
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 20px;
    letter-spacing: 1px;
    transition: all 0.6s ease-out;
  }

  .btn-bottom1
  {
    position: absolute;
    opacity: 0;
    width: 90%;
    top: 46px;
    left: 15px;
    border: 1px solid white;
    background-color: black;
    transform: translateX(-80%);
    transition: all 0.6s ease-in;
  }

  .right1
  {
    position: absolute;
    opacity: 0;
    top: 23px;
    left: 105px;
    width: 45%;
    border: 1px solid white;
    background-color: black;
    transform: rotate(-90deg) translateX(50%);
    transition: all 0.6s ease-in;
  }

  &:hover
  {
    .butt1
    {
      background:white;
      color:black;
      border: 1px solid gray;
    }

    .btn-bottom1
    {
      transform: translateX(0%);
      opacity: 1;
    }

    .right1
    {
      transform: rotate(-90deg) translateX(0%);
      opacity: 1;
    }
  }
}

.testimonialback{
