.bgimageresidential{
 height:80vh;
 width:80vw;
 position: relative;
}
.header{
  font-family: 'Open Sans Condensed', sans-serif;
	font-size: 40px;
	letter-spacing: 0.5px;
}
.description-container{
  position: absolute;
  top:15%;
  left:45%;
  font-family: 'Open Sans Condensed', sans-serif;
	font-size: 25px;
	letter-spacing: 0.5px;
  color: white;
  height:auto;
}

.list
{
  list-style: none;
  text-align: left;
}
.tint{
  height: 100%;
  width: 100%;
  background: black;
  opacity: 0.2;
  display: grid;
  grid-template-columns: 40px 50px auto 50px 40px;
  grid-template-rows: 17% 60% auto;
}
.gridElement
{
  border: 1.3px solid #E1E3E2;
}
.category-button
{
  position: relative;
  width: 150px;

  .butt
  {
    width: 120px;
    height: 50px;
    border: none;
    color: white;
    background: black;
    // border: 1px solid black;
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 20px;
    letter-spacing: 1px;
    transition: all 0.6s ease-out;
  }

  .btn-bottom
  {
    position: absolute;
    opacity: 0;
    width: 90%;
    top: 46px;
    left: 15px;
    border: 1px solid black;
    background-color: black;
    transform: translateX(-80%);
    transition: all 0.6s ease-in;
  }

  .right
  {
    position: absolute;
    opacity: 0;
    top: 23px;
    left: 105px;
    width: 45%;
    border: 1px solid black;
    background-color: black;
    transform: rotate(-90deg) translateX(50%);
    transition: all 0.6s ease-in;
  }

  &:hover
  {
    .butt
    {
      background: white;
      color: black;
      border: 1px solid gray;
    }

    .btn-bottom
    {
      transform: translateX(0%);
      opacity: 1;
    }

    .right
    {
      transform: rotate(-90deg) translateX(0%);
      opacity: 1;
    }
  }
}

@media only screen
   and (min-width :320px){
     .bgimageresidential{
      height:120vh;
      width:90vw;
     }
     .description-container{
       position: absolute;
       bottom:30%;
       right:23%;
       font-family: 'Open Sans Condensed', sans-serif;
       font-size:7px;
       letter-spacing: 0.5px;
       color: white;
       height:60vh;
     }
   }

@media only screen
   and (max-width : 768px){
     .description-container{
       position: absolute;
       top:10%;
       left:30%;
       font-family: 'Open Sans Condensed', sans-serif;
       	font-size: 25px;
       	letter-spacing: 0.5px;
       color: white;
     }
   }

@media only screen
   and (min-width : 1024px){
     .description-container{
       position: absolute;
       top:15%;
       left:38%;
       font-family: 'Open Sans Condensed', sans-serif;
     	font-size: 25px;
     	letter-spacing: 0.5px;
       color: white;
       text-align:left;
     }
     .bgimageresidential{
      height:100vh;
      width:80vw;
      position: relative;
     }

      }
